*,
*::before,
*::after {
   box-sizing: border-box;
}

body {
   font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
      Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
   line-height: 1.5;
   color: #2a2a2a;
}

/* img {
   display: block;
   max-width: 100%;
   height: auto;
} */

.search-form {
   display: flex;
   justify-content: center ;
   padding: 8px;
   margin-bottom: 24px;
}

.button {
   display: block;
   margin-left: auto;
   margin-right: auto;
}

.button.is-hidden {
   display: none;
}

.button>.spinner.is-hidden {
   display: none;
}

